import React, { Component } from 'react'
import './App.css';
import Sidebar from './component/Sidebar'
import Chat from './component/Chat'

class App extends Component {
  render() {
    return (
        <div className="app">
          <div className="app__body">
            {/* Sidebar */}
            <Sidebar />

            {/* Chat */}
            <Chat />
          </div>
        </div>
    )
  }
}

export default App